.paper {
  margin-top: 10%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 3px solid black;
  border-radius: 5px;
}

.avatar {
  margin: 10px;
  background-color: black;
}

.form {
  width: 70%;
  padding: 5%;
}

.form h1 {
  text-align: center;
}

.form Button {
  background-color: #4b54db;
}

.form Button:hover {
  background-color: #6971dd;
}

input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.submit {
  margin-top: 30px;
}

.options {
  color: blue;
  text-decoration: underline;
  margin-top: 15px;
}

.serverError {
  color: white;
  background-color: #ec5c5c;
  text-align: center;
  border-width: 2px;
  border-style: solid;
  border-color: red;
  border-radius: 5px;
  padding: 5px 0 5px;
}
